@import url("https://fonts.googleapis.com/css2?family=Playfair+Display+SC:wght@400;700&family=Poppins:wght@300;500;700&family=Roboto:wght@300;500;700&display=swap");

// ====== MIXINS ========//

@mixin list-null {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin playfair {
  font-family: "Playfair Display SC", serif;
}

@font-face {
  font-family: "Medium-font", sans-serif;
  src: url("/dist/fonts/Laila\ Medium.ttf");
}
// === VARIABLES ===

$primary-color: #ff63e8;
$secondary-color: #ffa6d7;

// ===== COMMON =====
html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  // @include playfair();
  font-family: "Medium-font", sans-serif;
  font-size: 16px;
  font-weight: 300;
}
h1,
h2,
h3,
h4,
h5,
h6,
header,
a {
  font-family: "Playfair Display SC", serif;
}
.bg-body-tertiary {
  --bs-bg-opacity: 0;
  background-color: inherit;
}

.bg-light {
  background-color: #ffffff !important;
  box-shadow: 0px 1px 3px 1px rgba(246, 116, 209, 0.2);
}
.btn-light {
  box-shadow: 0px 5px 10px 2px $secondary-color;
}

hr {
  margin: 50px auto;
  max-width: 350px;
  border-top: 2px solid $primary-color;
}
a {
  text-decoration: none;
  color: #000000;
}

.text-small {
  font-size: 12px;
}

// ==== HEADER ==== //
.navbar {
  &-nav {
    align-items: center;
  }
  &-collapse {
    flex-grow: 0;
  }
}
.nav-link {
  &:hover {
    border-bottom: 1px solid $secondary-color;
  }
}

// ==== PREVIEW ==== //
.preview {
  background-color: #ffffff;

  &-img {
    border-bottom: 1px solid $secondary-color;
  }
}
.socials {
  @include list-null();

  &-links {
    & svg {
      margin: 0 auto;
      width: 40px;
      height: auto;
      &:hover {
        transform: scale(1.25);
        transition: 0.2s ease;
      }
    }
  }
}
.swiper {
  &-wrapper {
    transition-timing-function: linear;
  }
  &-slide {
    img {
      width: 64px;
      display: block;
      object-fit: cover;
    }
  }
}
.zodiac-signs {
  &-img {
    width: 64px;
  }
}
.insta-link {
  text-transform: uppercase;
  border: 1px solid $secondary-color;

  &:hover {
    border: 1px solid $primary-color;
  }
}

// ==== ABOUT ==== //
.about {
  &-quality {
    padding: 30px 10px;
    background: rgba(255, 255, 255, 0);
    border-radius: 16px;

    border: 1px solid rgba(0, 255, 242, 1);

    &__item {
      max-width: 500px;
      margin: 0 auto;
      text-align: center;
    }
  }

  &-values {
    align-items: center;
    &_title {
    }
    &_item {
      width: 50%;
      padding: 20px;
      margin-bottom: 15px;
      border-radius: 30px;
      color: #000000;
      background-color: $primary-color;
      background-image: linear-gradient(
        315deg,
        $primary-color 0%,
        $secondary-color 74%
      );
      box-shadow: 5px 5px 20px rgb(203 205 211 / 10%);
      text-transform: uppercase;
      --border-size: 3px;
      --border-angle: 0turn;
      background-image: conic-gradient(
          from var(--border-angle),
          #ffffff,
          rgb(255, 255, 255) 50%,
          #ffffff
        ),
        conic-gradient(
          from var(--border-angle),
          transparent 20%,
          rgb(0, 255, 242),
          rgb(255, 93, 225)
        );
      background-size: calc(100% - (var(--border-size) * 2))
          calc(100% - (var(--border-size) * 2)),
        cover;
      background-position: center center;
      background-repeat: no-repeat;

      animation: bg-spin 3s linear infinite;
      @keyframes bg-spin {
        to {
          --border-angle: 1turn;
        }
      }
      &:hover {
        animation-play-state: paused;
      }
      &:last-child {
        margin-bottom: 0px;
      }
      p {
        text-align: center;
        margin-bottom: 0;
      }
    }
  }
}
@property --border-angle {
  syntax: "<angle>";
  inherits: true;
  initial-value: 0turn;
}
.services {
  overflow: hidden;
  .solar-wrapper {
  }
  @media (max-width: 992px) {
    .solar-wrapper {
      height: 450px;
    }
  }
  @media (max-width: 576px) {
    .solar-wrapper {
      height: 250px;
    }
  }
}

// ==== VALUES ==== //

.accordion {
  --bs-accordion-border-color: none;
  // --bs-accordion-btn-focus-box-shadow: 0 0 0 0.25rem $secondary-color;

  --bs-accordion-btn-padding-y: 1.5rem;

  &-item {
  }
  &-button {
    box-shadow: $secondary-color 0px 3px 5px 2px;
  }
}
.accordion-button:not(.collapsed) {
  background-color: white;
}

// === FOOTER === //

.footer {
  .row > * {
    width: auto;
  }

  @media (max-width: 370px) {
    &-logo {
      display: none !important  ;
    }
    .row {
      justify-content: center !important;
    }
  }
}
